<template>
  <header>
    <div class="l-content">
      <!-- 左边栏 -->
      <el-button
        plain
        icon="el-icon-menu"
        size="mini"
        @click="handleMenu"
      ></el-button>

      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!--很明显 首页 一定是存在的 所以这里直接写死-->
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <!--第二级菜单名称 就要看左侧组件有没有点击指定菜单，没有那就只显示首页 点击就显示当前菜单名称-->
        <el-breadcrumb-item :to="current.path" v-if="current">{{
          current.label
        }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- 右边栏 -->
    <div class="r-content">
      <el-dropdown trigger="click" size="mini">
        <span><img class="user" src="../assets/3.png" alt="" /></span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
export default {
  name: "commonHeader",
  data() {
    return {
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
    };
  },

  methods: {
    handleClick() {
      alert("button click");
    },
    ...mapMutations({ handleMenu: "tab/collapseMenu" }),

    logout() {
      this.$store.commit("user/clearToken");
      this.$store.commit("tab/clearMenu");
      this.$router.push("/login");
    },
    //编程式路由导航
    pushItem(item) {
      this.$router.push({ name: item });
      console.log(this.$router);
    },
  },
  computed: {
    ...mapState({ current: (state) => state.tab.currentMenu }),
  },
};
</script>

<style scoped>
header {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

h4 {
  float: right;
  margin: 0;
  margin-left: 20px;
  font-weight: 400;
}
h5 {
  border-left: 1px solid rgb(255, 255, 255);
  margin-left: 20px;
  padding-left: 20px;
  font-weight: normal;
}

.l-content {
  width: 300px;
  display: flex;
  align-items: center;
}
.el-button {
  margin-left: 20px;
  margin-right: 20px;
}

.user {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
</style>