<template>
  <div>
    <el-menu
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      :default-active="$route.path"
      ><h3>{{ isCollapse ? "后台" : "通用后台管理系统" }}</h3>
      <!-- 一级目录 -->
      <el-menu-item
        @click="pushItem(item)"
        v-for="item in noChildren"
        :index="item.path + ''"
        :key="item.path"
      >
        <i :class="'el-icon-' + item.icon"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
      <!-- 多级目录 -->
      <el-submenu
        v-for="item in hasChildren"
        @click="pushItem(item)"
        :index="item.path + ''"
        :key="item.path"
      >
        <template slot="title">
          <i :class="'el-icon-' + item.icon"></i>
          <span slot="title">{{ item.label }}</span>
        </template>
        <el-menu-item-group
          v-for="(subItem, subIndex) in item.children"
          :key="subItem.path"
        >
          <el-menu-item :index="subIndex + ''" @click="pushItem(subItem)">{{
            subItem.label
          }}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "commonAside",
  data() {
    return {
      menu: [],
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    //编程式路由导航
    pushItem(item) {
      this.$store.commit("tab/selectMenu", item);
      this.$router.push({ name: item.name });
    },
  },

  computed: {
    noChildren() {
      return this.asyncMenu.filter((item) => !item.children);
    },
    hasChildren() {
      return this.asyncMenu.filter((item) => item.children);
    },
    ...mapState({ isCollapse: "tab/isCollapse" }),
    asyncMenu() {
      return this.$store.state.tab.menu;
    },
  },
};
</script>

<style  scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  border-right: none;
  height: 100%;
  width: 200px;
  min-height: 400px;
  text-align: left;
}
h3 {
  color: white;
  text-align: center;
  line-height: 48px;
}
</style>